import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ContextService } from './context.service';
import { CapacitorHttp } from '@capacitor/core';

@Injectable({
  providedIn: 'root',
})
export class InfoEmergenciaService {
  constructor(private http: HttpClient, private context: ContextService) {}

  async saveInfoEmergencia(infoEmergencia: {}): Promise<{ data: any; status: number }> {
    try {
      return new Promise((resolve, reject) => {
        const url = this.context.getEnvUrl().call(null, 'empresa') + '/Emergencia/Guardar-Informacion-Emergencia-Usuario';
        console.log(url, 'url');
        console.log(infoEmergencia, 'infoEmergencia');
        CapacitorHttp.post({
          url: url,
          headers: {
            'Content-Type': 'application/json;charset=UTF-8',
          },
          data: infoEmergencia,
        })
          .then(response => {
            console.log(response, 'response');
            resolve({ data: response.data, status: response.status });
          })
          .catch(error => {
            console.log(error, 'error');
            reject({ data: error, status: 500 });
          });
      });
    } catch (error) {
      return { data: error, status: 500 };
    }
  }

  getEPS() {
    return this.http.get<any[]>('/assets/data/eps.json');
  }
}
